'use client';

import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import { z } from 'zod';
import { Button } from '@/components/ui/button';
import {
  Card,
  CardContent,
  CardDescription,
  CardHeader,
  CardTitle,
} from '@/components/ui/card';
import {
  Form,
  FormControl,
  FormField,
  FormItem,
  FormLabel,
  FormMessage,
} from '@/components/ui/form';
import { Input } from '@/components/ui/input';
import { useFirestore, useUser, useMemoFirebase } from '@/firebase';
import { doc } from 'firebase/firestore';
import { setDocumentNonBlocking } from '@/firebase/non-blocking-updates';
import { useToast } from '@/hooks/use-toast';
import { useDoc } from '@/firebase/firestore/use-doc';
import { Tabs, TabsContent, TabsList, TabsTrigger } from "@/components/ui/tabs"
import { Separator } from '@/components/ui/separator';

const profileFormSchema = z.object({
  firstName: z.string().min(1, 'First name is required'),
  lastName: z.string().min(1, 'Last name is required'),
});

const passwordFormSchema = z.object({
    currentPassword: z.string().min(6, "Password must be at least 6 characters"),
    newPassword: z.string().min(6, "Password must be at least 6 characters"),
    confirmPassword: z.string().min(6, "Password must be at least 6 characters"),
}).refine(data => data.newPassword === data.confirmPassword, {
    message: "New passwords don't match",
    path: ["confirmPassword"],
});


type UserProfile = {
  firstName: string;
  lastName: string;
  email: string;
};

function ProfileForm({ user, userProfile, userDocRef }: { user: any, userProfile: UserProfile | null, userDocRef: any }) {
    const { toast } = useToast();
    const form = useForm<z.infer<typeof profileFormSchema>>({
        resolver: zodResolver(profileFormSchema),
        values: {
            firstName: userProfile?.firstName || '',
            lastName: userProfile?.lastName || '',
        },
    });

    const onSubmit = (values: z.infer<typeof profileFormSchema>) => {
        if (!userDocRef) return;
        
        setDocumentNonBlocking(userDocRef, { 
          ...values,
          email: user?.email,
          id: user?.uid,
        }, { merge: true });
    
        toast({
          title: 'Profile Updated',
          description: 'Your profile has been successfully updated.',
        });
    };

    return (
        <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-6">
                <div className="space-y-2">
                    <h3 className="text-xl font-semibold">Personal Information</h3>
                    <p className="text-sm text-muted-foreground">Update your personal details here.</p>
                </div>
                <Separator/>
                <div className="grid grid-cols-2 gap-4">
                    <FormField
                    control={form.control}
                    name="firstName"
                    render={({ field }) => (
                        <FormItem>
                        <FormLabel>First Name</FormLabel>
                        <FormControl>
                            <Input {...field} />
                        </FormControl>
                        <FormMessage />
                        </FormItem>
                    )}
                    />
                    <FormField
                    control={form.control}
                    name="lastName"
                    render={({ field }) => (
                        <FormItem>
                        <FormLabel>Last Name</FormLabel>
                        <FormControl>
                            <Input {...field} />
                        </FormControl>
                        <FormMessage />
                        </FormItem>
                    )}
                    />
                </div>
                <FormItem>
                    <FormLabel>Email</FormLabel>
                    <FormControl>
                        <Input readOnly disabled value={user.email || ''} />
                    </FormControl>
                </FormItem>
                <Button type="submit">Save Changes</Button>
            </form>
        </Form>
    );
}

function AccountSettingsForm() {
    const { toast } = useToast();
    const passwordForm = useForm<z.infer<typeof passwordFormSchema>>({
        resolver: zodResolver(passwordFormSchema),
        defaultValues: { currentPassword: "", newPassword: "", confirmPassword: "" },
    });

    const onPasswordSubmit = (values: z.infer<typeof passwordFormSchema>) => {
        console.log("Password change requested:", values);
        toast({
          title: "Password Change Requested",
          description: "This is a demo. No actual password change has occurred.",
        });
        passwordForm.reset();
    };

    return (
        <div className="space-y-8">
            {/* Change Password Section */}
            <Form {...passwordForm}>
                <form onSubmit={passwordForm.handleSubmit(onPasswordSubmit)} className="space-y-6">
                     <div className="space-y-2">
                        <h3 className="text-xl font-semibold">Change Password</h3>
                        <p className="text-sm text-muted-foreground">For your security, you must enter your current password to make changes.</p>
                    </div>
                    <Separator/>
                    <FormField
                        control={passwordForm.control}
                        name="currentPassword"
                        render={({ field }) => (
                            <FormItem>
                            <FormLabel>Current Password</FormLabel>
                            <FormControl><Input type="password" {...field} /></FormControl>
                            <FormMessage />
                            </FormItem>
                        )}
                    />
                    <div className="grid grid-cols-2 gap-4">
                        <FormField
                            control={passwordForm.control}
                            name="newPassword"
                            render={({ field }) => (
                                <FormItem>
                                <FormLabel>New Password</FormLabel>
                                <FormControl><Input type="password" {...field} /></FormControl>
                                <FormMessage />
                                </FormItem>
                            )}
                        />
                        <FormField
                            control={passwordForm.control}
                            name="confirmPassword"
                            render={({ field }) => (
                                <FormItem>
                                <FormLabel>Confirm New Password</FormLabel>
                                <FormControl><Input type="password" {...field} /></FormControl>
                                <FormMessage />
                                </FormItem>
                            )}
                        />
                    </div>
                    <Button type="submit">Change Password</Button>
                </form>
            </Form>

            {/* Delete Account Section */}
            <div className="space-y-6">
                <div className="space-y-2">
                    <h3 className="text-xl font-semibold">Delete Account</h3>
                    <p className="text-sm text-muted-foreground">Permanently delete your account and all associated data. This action cannot be undone.</p>
                </div>
                <Separator/>
                <Button variant="destructive">Delete My Account</Button>
            </div>
        </div>
    );
}


export default function ProfilePage() {
  const { user, isUserLoading } = useUser();
  const firestore = useFirestore();

  const userDocRef = useMemoFirebase(
    () => (firestore && user ? doc(firestore, 'users', user.uid) : null),
    [firestore, user]
  );

  const { data: userProfile, isLoading: isProfileLoading } = useDoc<UserProfile>(userDocRef);

  if (isUserLoading || isProfileLoading) {
    return <div className="container mx-auto py-12"><p>Loading profile...</p></div>;
  }

  if (!user) {
    return <div className="container mx-auto py-12"><p>Please log in to view your profile.</p></div>;
  }

  return (
    <div className="container mx-auto py-8 md:py-12">
        <div className="mb-8">
            <h1 className="text-3xl md:text-4xl font-headline font-bold">My Profile</h1>
            <p className="text-muted-foreground">Manage your account settings and personal information.</p>
        </div>
      <Tabs defaultValue="profile" className="grid grid-cols-4 gap-8">
        <TabsList className="flex-col justify-start items-stretch h-auto col-span-1">
          <TabsTrigger value="profile" className="justify-start">Profile</TabsTrigger>
          <TabsTrigger value="settings" className="justify-start">Account Settings</TabsTrigger>
        </TabsList>
        <div className="col-span-3">
            <Card>
                <CardContent className="p-6">
                    <TabsContent value="profile">
                        <ProfileForm user={user} userProfile={userProfile} userDocRef={userDocRef} />
                    </TabsContent>
                    <TabsContent value="settings">
                        <AccountSettingsForm/>
                    </TabsContent>
                </CardContent>
            </Card>
        </div>
      </Tabs>
    </div>
  );
}
